
#app {
  font-family: Microsoft Yahei, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: grey;
  height: 100%;
}
header {
  position: relative;
	height: 36px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
/*	justify-content: space-between;
  flex-direction: row;*/
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
  background-color: #ffff8c;
	border-bottom: 2px #ffff8c solid;
  margin-bottom: 7px;
}
#logo {
	color: #ff8585;
  font-size: 20pt;
  text-shadow:2pt 2pt 2pt lightgrey;
}
#logo:hover {
  cursor: pointer;
}
#nav-container {
  color: white;
  font-size: 17pt;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 250pt;
  margin-left: 30px;
}
#nav-container *:hover {
  color: lightgrey;
  cursor:pointer;
}
.search-box-hidden {
  visibility: hidden;
}
.search-box-show {
  visibility: visible;
}
input {
  border-radius: 5px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  outline: 0;
  height: 25px;
  border: 1px #DADADA solid;
  padding-left: 5px;
  color: grey;
}
input[placeholder] {
  padding-left: 5px;
  color: grey;
}
.greeting {
  width: 80px;
  position: absolute;
  right: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack:justify;
      -ms-flex-pack:justify;
          justify-content:space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.greeting *:hover {
  color: lightgrey;
}
.btn {
  padding:0 10px 0 10px;
	background-color: white;
	border: solid 1px #ffff8c;
	border-radius:5px;
	word-break:keep-all;           /* 不换行 */
	white-space:nowrap; 
	color: #ff8585;
}
.btn:hover {
	color: yellow;
	cursor:pointer
}
a {
    text-decoration:none;
}
a:visited {
    color: white;
}
h1 {
  color: #ffff8c;
}



